@import url(https://fonts.googleapis.com/css2?family=RocknRoll+One&display=swap);
.homepage {
 width: 100vw;
 max-width: 1000px;
 min-height: 100vh;
 margin: auto;
 margin-top: 0;
 margin-bottom: 0;
 background-color: tan;
 color: whitesmoke;
}
.hompageTitle {
 padding-top: 10px;
 text-align: center;
 margin-top: 0;
}
.hotelImage {
 width: 160px;
 display: inline;
 border-radius: 30px;
 height: 120px;
}

.hotelGallery {
 display: flex;
 justify-content: space-evenly;
 flex-wrap: wrap;
}
.hotelCard {
 background-image: url(/hotel-app/static/media/cardBackground.2cc37a11.jpg);
 background-size: 160px 160px;
 display: flex;
 flex-direction: column;
 text-align: center;
 width: 160px;
 margin-bottom: 15px;
 border-radius: 10px;
 border-top-left-radius: 30px;
 border-top-right-radius: 30px;
 color: rgb(177, 150, 114);
 font-weight: bold;
 font-size: 20px;
}

.hotelPage {
 min-height: 100vh;
 max-width: 1000px;
 background-color: tan;
 padding-left: 5vw;
 color: whitesmoke;
 margin: auto;
}

.hotelPageTitle {
 margin-top: 0;
 padding-top: 10px;
 text-align: center;
}
.hotelPageImage {
 margin-bottom: -15px;
 border: solid;
 width: 75vw;
 max-width: 500px;
}

.address {
 font-size: 25px;
 margin-top: 40px;
}

.callBtn {
 margin-top: 10px;
 background-image: url(/hotel-app/static/media/cardBackground.2cc37a11.jpg);
 background-size: 160px 160px;
 color: tan;
 font-size: 40px;
 font-weight: bold;
 border-radius: 20px;
 margin-bottom: 20px;
}
.callBtn:active {
 background-color: whitesmoke;
 background-image: none;
}

body {
 margin: 0;
 font-family: 'RocknRoll One', sans-serif;
}

#root {
 margin: 0;
}

